@charset "UTF-8";
*,
*:before,
*:after {
  box-sizing: border-box; }

html {
  user-select: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

h1,
p,
ul {
  margin: 0;
  padding: 0; }

h1 {
  font-size: inherit; }

ul,
li {
  list-style: none; }

a {
  color: inherit; }

table {
  border-collapse: collapse; }

button,
input,
textarea {
  border: 0;
  outline: 0;
  margin: 0;
  padding: 0;
  background-color: transparent;
  color: inherit;
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  line-height: inherit;
  text-transform: inherit; }

button {
  cursor: pointer; }
  button:disabled {
    cursor: default; }

textarea {
  resize: none; }

html,
body {
  min-height: 100vh; }

body {
  display: flex;
  justify-content: center;
  align-items: center;
  align-items: stretch;
  background-color: #193e2d;
  color: #ffffff; }

#game {
  font-weight: bold;
  margin: 0 auto;
  padding: 15px;
  padding-top: 65px;
  text-align: center;
  text-transform: uppercase;
  max-width: 540px;
  width: 100%; }

h1 {
  font-size: 1.5rem; }

a[target='_blank']::after {
  content: ' â†—'; }

button,
input,
textarea {
  background-color: rgba(255, 255, 255, 0.04);
  color: rgba(255, 255, 255, 0.5);
  height: 50px;
  width: 200px; }
